<template>
  <div>
    <div style="padding: 20px;overflow: hidden;background-color: #fff;margin-bottom: 10px">
      <div style="float:left;">
        <p style="line-height: 40px;font-size: 14px;font-weight: 700"></p>
        <p style="color: #9c9c9c">2020-2-1</p>
      </div>
      <img style="float: right" src="./mmexport1588912806444.png" height="60" width="60"/>
    </div>
    <van-grid :border="false" :column-num="3">
      <van-grid-item>
        <p style="color: #159bff;font-size: 13px;font-weight: bold">9999+</p>
        <p style="padding-top: 5px;font-size: 14px"></p>
      </van-grid-item>
      <van-grid-item>
        <p style="color: #159bff;font-size: 13px;font-weight: bold">9999+</p>
        <p style="padding-top: 5px;font-size: 14px"></p>
      </van-grid-item>
      <van-grid-item>
        <p style="color: #159bff;font-size: 13px;font-weight: bold">9999+</p>
        <p style="padding-top: 5px;font-size: 14px"></p>
      </van-grid-item>
    </van-grid>
    <!---->
    <div style="margin-top: 10px">
      <van-panel title="">
        <div style="padding: 20px 20px">
          <div v-html="value[0] ? value[0] : explain" style="line-height: 30px;color: mediumaquamarine"></div>
        </div>
      </van-panel>
      <div style="margin-top: 20px;position: fixed;bottom:20px;left: 15px;right: 10px">
        <el-cascader :options="options" v-model="value" size="small " style="float: left" placeholder="" :show-all-levels="false"></el-cascader>
        <el-button type="primary" size="small " style="float: right" @click="pay"></el-button>
      </div>
    </div>
  </div>
</template>
<script>
    import { MemberBuy} from '../api/apilist'
    import { Notify } from 'vant';
    import { Dialog } from 'vant';
    export default {
        name: "Member",
      data() {
        return {
          value:{},
          explain:'',
          switch1: false,
          switch2: false,
          options:[
            {value: '<p>1.10</p>\n'+'<p>2.(24)</p>', label: '',
              children: [
                {value: 1, label: '9.9/'},
                {value:2, label: '15.9/3',},
                {value: 3, label: '25.9/',}]
            },
            {value: '<p>1.</p>\n'+'<p>2.(24)</p>', label: '',
              children: [{value:4, label: '19.9'},{value: 5, label: '38.8'},{value:6, label: '49.9'}]},
            {value: '<p>1.,10</p>\n'+'<p>2.24</p>', label: '',
              children: [{value: 7, label: '99.9',}]},
          ],
          data:[]
        };
      },
      components: {
        [Dialog.Component.name]: Dialog.Component,
      },
      methods:{
        pay(){
          if(this.value[1]){
            MemberBuy(this.value).then((res=>{
              Dialog.confirm({
                title: '',
                message: ``,
              })
                .then(() => {
                  window.location.href=res.payurl;
                })
                .catch(() => {

                });
            }))
          }else{
            Notify({ type: 'danger', message: '' });
          }
        }
      }
    }
</script>
<style scoped>

</style>



